.grid {
	max-width: 1280px;
	margin-left: auto;
	margin-right: auto;
}

.grid a:hover {
	text-decoration: none;
}

.bgrid > * {
	width: 100%;
}

.bgrid-right {
}

.grid-section {
	padding: 16px;
	position: relative;
	border-top: 1px solid var(--color-border);
}

.grid-section:first-child {
	border-top: 0;
}

.grid-section:last-child {
    padding-bottom: 0;
}

.grid-section.title {
	border-top: 0;
}

.grid-section.title.hash-no-title {
	padding-top: 1.5rem;
}

.grid-section.title.hash-no-title .gs-title {
	display: none;
}

.grid-section.title.hash-image-center .kg-image-card {
    text-align: center;
}

.grid-section.title.hash-image-center .kg-image-card img {
  width: auto;
  display: inline-block;
}

.grid-item {
	position: relative;
	margin-bottom: 32px;
}

.grid-item:last-child {
	margin-bottom: 0;
}

.grid-item::after {
	content: "";
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	bottom: -16px;
	border-bottom: 1px solid var(--color-border);
}

.grid-item:last-child::after {
	border: 0;
}

.gi-image-link {
	display: block;
}

.gi-image-cropped {
	display: none;
	min-width: 70px;
	min-height: 70px;
	color: #fff;
	font-size: 1.1rem;
	overflow: hidden;
	font-family: var(--font-sans-serif);
	width: 70px;
	height: 70px;
	margin-top: 4px;
	object-fit: cover;
	background-size: cover;
	background-position: center center;
	background-color: #dfdfdf;
}

.gi-title-link {
	text-decoration: none;
}

.gi-excerpt {
	color: #666;
	margin-top: 0.5rem;
}

.gi-author {
	display: block;
	margin-top: 1.5rem;
	font-size: 0.75rem;
	font-family: "cooper hewitt";
	font-weight: 700;
	text-transform: uppercase;
}

.gi-info {
	flex: 1 1 auto;
}

.gi-title {
	font-weight: 400;
}

.cropped {
  display: flex;
}

.cropped .gi-image-cropped {
	display: block;
	margin-right: 16px;
}

.cropped.big .gi-image-cropped {
	min-width: 160px;
	min-height: 100px;
	width: 160px;
	height: 100px;
	background-color: #dfdfdf;
	font-family: "cooper hewitt";
	display: flex;
	justify-content: center;
	align-items: center;
	font-weight: bold;
	color: white;
	font-size: 4rem;
	overflow: hidden;
}

.cropped .gi-image {
	display: none;
}

.cropped.right .gi-header {
	order: 1;
}

.cropped .gi-info {
	flex: 1 1 auto;
}

.cropped .gi-excerpt {
	display: none;
}

.cropped.right .gi-image-cropped {
	display: block;
	margin-left: 16px;
	margin-right: 0;
}

.gs-title {
	position: relative;
	font-weight: 400;
	margin-top: 0.75rem;
	margin-bottom: 24px;
	text-transform: uppercase;
}

.gs-title::after {
	content: "";
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	bottom: -8px;
	border-bottom: 1px solid var(--color-border);
}

.tcenter {
	text-align: center;
}

.tcenter .gi-info {
}

.tcenter .gi-title {
	font-size: 1.8rem;
	margin-top: 0.75rem;
}

.tcenter .gi-excerpt {
	font-size: 1.1rem;
	color: #000;
}

.tright .gi-title {
	font-size: 1.1rem;
}

.tleft .gi-title {
	font-size: 1.4rem;
	margin-top: 0.75rem;
}

.tleft .gi-excerpt {
	display: none;
}

.tmiddle .gi-title {
	font-size: 1.2rem;
}

.tbottom .gi-title {
	font-size: 1.6rem;
	margin-top: 0.75rem;
}

.tbottom .tbottom-two {
	position: relative;
	margin-top: 32px;
}

.tbottom .tbottom-two::before {
	content: "";
	position: absolute;
	top: -16px;
	left: 0;
	right: 0;
	bottom: auto;
	border-top: 1px solid var(--color-border);
}

.tbottom .gi-excerpt {
	display: none;
}

.tmore .gi-title {
	font-size: 1.3rem;
}

.tmore .gi-image-cropped {
	margin-top: 8px;
}

.tmore .gi-author {
	margin-top: 2rem;
}

.tmore .gi-excerpt {
	display: none;
}

.bgrid-left .read-more {
	margin-top: 1rem;
	margin-bottom: 0.5rem;
	text-align: center;
}

.tnewsletter .form-text {
	font-family: "cooper hewitt";
}

.tnewsletter .form-group > * + * {
	margin-top: 0.75rem;
}

.tnewsletter .form-group {
	display: flex;
	flex-direction: column;
}

@media (min-width: 580px) {
	.grid {
		display: flex;
		flex-wrap: wrap;
		width: calc(100%);
		margin-left: auto;
		margin-right: auto;
		padding-top: 16px;
		padding-left: 16px;
		padding-right: 16px;
	}

	.grid-section {
		width: 100%;
		border: 0;
		padding: 0;
	}

	.tcenter {
		flex-basis: 50%;
	}

	.tright {
		margin-left: 32px;
		flex-basis: calc(50% - 32px);
	}

	.tright::before {
		content: "";
		position: absolute;
		top: 0;
		bottom: 0;
		left: -16px;
		right: auto;
		border-left: 1px solid var(--color-border);
	}

	.tleft {
		display: flex;
		margin-top: 32px;
	}

	.tleft::before {
		content: "";
		position: absolute;
		top: -16px;
		left: 0;
		right: 0;
		bottom: auto;
		border-top: 1px solid var(--color-border);
	}

	.tleft > * {
		flex-basis: 50%;
	}

	.tleft > :first-child {
		margin: 0;
		margin-right: 32px;
	}

	.tleft > :first-child::after {
		content: "";
		position: absolute;
		top: 0;
		bottom: 0;
		left: auto;
		right: -16px;
		border-bottom: 0;
		border-right: 1px solid var(--color-border);
	}

	.tmiddle {
		display: flex;
		flex-wrap: wrap;
		margin-top: 16px;
	}

	.tmiddle > * {
		display: block;
		flex-basis: calc(50% - 16px);
	}

	.tmiddle > *::after{
		border-bottom: 0;
	}

	.tmiddle .cropped.right .gi-image-cropped {
		display: none;
	}

	.tmiddle .gi-image {
		display: block;
		height: 170px;
		width: 100%;
		object-fit: cover;
	}

	.tmiddle .gi-title {
		font-size: 1.4rem;
		margin-top: 0.75rem;
	}

	.tmiddle > :nth-child(2n+1) {
		margin-right: 32px;
		margin-bottom: 16px;
	}

	.tmiddle > :nth-child(n+3) {
		margin-bottom: 0;
	}

	.tmiddle > :nth-child(2n+1)::after {
		content: "";
		position: absolute;
		top: 0;
		bottom: 0;
		left: auto;
		right: -16px;
		border-bottom: 0;
		border-right: 1px solid var(--color-border);
	}

	.tbottom {
		display: flex;
		margin-top: 32px;
	}

	.tbottom::before {
		content: "";
		position: absolute;
		top: -16px;
		left: 0;
		right: 0;
		bottom: auto;
		border-top: 1px solid var(--color-border);
	}

	.tbottom > * {
		position: relative;
		flex-basis: 50%;
	}

	.tbottom > :first-child {
		margin-right: 32px;
	}

	.tbottom > :first-child::after {
		content: "";
		position: absolute;
		top: 0;
		bottom: 0;
		left: auto;
		right: -16px;
		border-bottom: 0;
		border-right: 1px solid var(--color-border);
	}

	.tbottom .gi-title {
		font-size: 1.4rem;
		margin-top: 0;
	}

	.tbottom .tbottom-one > * {
		display: flex;
	}

	.tbottom .tbottom-one .gi-header {
		order: 1;
	}

	.tbottom .tbottom-one .gi-image {
		display: none;
	}

	.tbottom .tbottom-one .gi-image-cropped {
		margin-left: 16px;
		display: block;
	}

	.tbottom .tbottom-two {
		margin-top: 0;
	}

	.tbottom .tbottom-two::before {
		border-top: 0;
	}

	.tbottom .tbottom-two > * {
		display: flex;
	}

	.tbottom .tbottom-two .gi-header {
		order: 1;
	}

	.tbottom .tbottom-two .gi-image {
		display: none;
	}

	.tbottom .tbottom-two .gi-image-cropped {
		display: block;
		margin-left: 16px;
	}

	.bgrid-left .read-more {
		margin-top: 1.5rem;
		margin-bottom: 0.5rem;
	}
}

@media (min-width: 780px) {
	.tleft > * {
		display: flex;
	}

	.tleft .gi-header {
		order: 1;
	}

	.tleft .gi-title {
		font-size: 1.4rem;
		margin-top: 0.75rem;
	}

	.tleft .gi-image-cropped {
		width: 150px;
		height: 110px;
		min-width: 150px;
		min-height: 110px;
		display: block;
		margin-left: 16px;
	}

	.tleft .gi-image {
		display: none;
	}

	.tmiddle {
		margin-top: 32px;
	}

	.tmiddle::before {
		content: "";
		position: absolute;
		top: -16px;
		left: 0;
		right: 0;
		bottom: auto;
		border-top: 1px solid var(--color-border);
	}

	.tmiddle > * {
		flex-basis: calc(25% - 24px);
		margin-bottom: 0;
	}

	.tmiddle > :nth-child(2n+1) {
		margin-right: 0;
	}

	.tmiddle > :nth-child(2n+1)::after {
		border-right: 0;
	}

	.tmiddle > :not(:last-child) {
		margin-right: 32px;
		margin-bottom: 0;
	}

	.tmiddle > :not(:last-child)::after {
		content: "";
		position: absolute;
		top: 0;
		bottom: 0;
		left: auto;
		right: -16px;
		border-bottom: 0;
		border-right: 1px solid var(--color-border);
	}

	.tmiddle .gi-title {
		font-size: 1.2rem;
		margin-top: 0.5rem;
	}

	.tmiddle .gi-excerpt {
		display: block;
	}

	.bgrid {
		display: flex;
	}

	.bgrid > * {
		position: relative;
	}

	.bgrid-left {
		flex-basis: calc(65% - 16px);
	}

	.bgrid-left .read-more {
		margin-top: 1.5rem;
		margin-bottom: 2rem;
	}

	.bgrid-right {
		flex-basis: calc(35% - 16px);
	}

	.bgrid > :first-child {
		margin-right: 32px;
	}

	.tmore .gi-excerpt {
		display: block;
	}

}

@media (min-width: 980px) {
	.tcenter {
		order: 1;
		flex-basis: calc(50% - 0px);
	}

	.tcenter .gi-title {
		font-size: 2rem;
	}

	.tcenter .gi-excerpt {
		font-size: 1.2rem;
	}

	.tright {
		order: 2;
		flex-basis: calc(25% - 32px);
	}

	.tright .cropped.right .gi-image-cropped {
		display: none;
	}

	.tleft {
		order: 0;
		flex-basis: calc(25% - 32px);
		margin-top: 0;
		flex-direction: column;
		margin-right: 32px;
	}

	.tleft > * {
		display: block;
	}

	.tleft .gi-image-cropped {
		display: none;
	}

  .tleft .gi-image {
		display: block;
    height: 170px;
    object-fit: cover;
    width: 100%;
	}

	.tleft::before {
		border-top: 0;
	}

	.tleft::after {
		content: "";
		position: absolute;
		top: 0;
		bottom: 0;
		left: auto;
		right: -16px;
		border-bottom: 0;
		border-right: 1px solid var(--color-border);
	}

	.tleft > :first-child {
		margin: 0;
		margin-bottom: 32px;
	}

	.tleft > :first-child::after {
		border-right: 0;
		content: "";
		position: absolute;
		top: auto;
		bottom: -16px;
		left: 0;
		right: 0;
		border-bottom: 1px solid var(--color-border);
	}

	.tmiddle {
		order: 3;
	}

	.tbottom {
		order: 4;
	}
}

@media (min-width: 1100px) {

	.tright .cropped.right .gi-image-cropped {
		display: block;
	}
}
